* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif, bold;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-inline: 50px;
  display: flex;
}

.nav {
  display: block;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}
